<template>
  <div class="main-1000">
    <div class="news">
      <div class="news-main">
        <div class="news-content">
          <div class="news-content-item" v-for="item in newsList" :key="item.id">
            <router-link :to="item.url" tag="a" :title="item.title">
              <p class="time"><span>{{item.date[2]}}</span><span>{{item.date[0]}}.{{item.date[1]}}</span></p>
              <div class="news-txt">
                <h4>{{item.title}}</h4>
                <p class="detail">
                  <span v-for="(news, ids) in item.paragraphs" :key="ids">{{news}}</span>
                </p>
              </div>
            </router-link>
          </div>
        </div>
        <div class="news-content news-lastest"><p class="view-more" @click="syncMore"></p></div>
      </div>
      <div class="news-adver">
        <!-- 微信 -->
        <div class="news-adver-item">
          <div class="weixin">
            <img src="http://www.cwea.org.cn/images/weixin.jpg" alt="">
            <p class="title">
              <span>扫描二维码关注</span>
              <span>《风能》官方微信公众号</span>
            </p>
          </div>
        </div>
        <!-- 企业新闻广告1 -->
        <div class="news-adver-item">
          <a href="http://www.cwea.org.cn/events_fengdianchang.html">
            <img src="http://www.cwea.org.cn/images/member-qiye-news_05.jpg" alt="">
          </a>
        </div>
        <!-- 广告位 -->
        <div class="news-adver-item">
          <a href="http://chinawindpower.org.cn" target=_blank>
            <img src="http://www.cwea.org.cn/images/member-qiye-news_06.jpg" alt="2019年北京国际风能大会暨展览会（CWP）">
          </a>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
import { transform2news } from '@/utils/date'

export default {
  name: 'Lastest',
  data () {
    return {
      newsList: []
    }
  },
  methods: {
    getData () {
      this.$axios.get('api/news/lastest').then((res) => {
        const list = res.data.list
        list.forEach((item) => {
          item.date = transform2news(item.date)
        })
        this.newsList = this.newsList.concat(list)
      })
    },
    syncMore () {
      this.getData()
    }
  },
  created () {
    this.getData()
  }
}
</script>

<style lang="scss" scoped>

</style>
